<template>
<div class="wrapper1">
    <div class="v-category" v-if="Rangeshow()">
      <v-category
        :options="category"
        @click="filterByCategory"
        @null="filterByCategory"
      />
    
    <div class="v-catalog">
        <label>Фильтр по цене</label>
                <input @input="sortByPrice"  v-model.number="minPrice" type="number" />
                <input @input="sortByPrice" v-model.number="maxPrice" type="number" />
                    <select v-model="selectSort">
                        <option v-for="rule in sortRules" :key="rule.key" :value="rule.key">{{ rule.title }}</option>
                    </select>
      </div>
      </div>
        <div class="v-catalog__list">
        <v-catalog-item
            filtered
            v-for="product in filtredProducts"
            :key="product.id"
            :product_data="product"
            @addToCart="addToCart"
        />
        </div>
    
</div>
</template>
<script>


import vCatalogItem from './v-catalog-item.vue'
import {mapActions, mapGetters} from 'vuex'
import vCategory from './v-category.vue'
import _ from 'lodash';

export default {
  name: 'v-catalog',
  components: {
    vCatalogItem,
    vCategory
  },
  data () {
        return {
          // product: [],
          limit: 8,
          minPrice: 0,
          maxPrice: 0,
          filtredProduct: [],
          sortRules: [
            { key :'good_id:asc', title: 'По порядку' },
            { key :'price_new:asc', title: 'По цене, сначала дешевые' },
            { key :'price_new:desc', title: 'По цене, сначала дорогие' }
        ],
          selectSort: 'good_id:asc'
        }
  },
  computed: {
    ...mapGetters([
      'PRODUCTS',
      'SEARCH_VALUE',
    ]),
      filtredProducts() {
      if (this.$route.path === '/') {
        return this.PRODUCTS.filter((el, index) => index < this.limit);
      }else {
        if (this.filtredProduct.length) {
          return this.filtredProduct
        }
        if (this.$store.state.filteredByCategory.length) {
          return this.$store.state.filteredByCategory
        }
        if (this.selectSort != 'good_id:asc') {
          return this.sort
        }
        // if (this.SEARCH_VALUE.length) {
        //   var filteree = [...this.PRODUCTS]
        //    return console.log(filteree.name.toLowerCase().includes(this.$store.state.searthValue.toLowerCase()))
        //   //  this.PRODUCTS.filter( this.SEARCH_VALUE == '' || product.name.toLowerCase().includes(this.$store.state.searthValue.toLowerCase()))
        // }
        else {
          return this.PRODUCTS
        }
}
    },
    sortKey: function() {
    return this.selectSort.split(':')[0];
    },
    sortDir: function() {
        return this.selectSort.split(':')[1];
    },
    sort() {
      var filtered = [...this.PRODUCTS]
      // Сортируем
    var sorted = _.sortBy(filtered, product => {
        return Number(product[this.sortKey]);
    });
 
    // При необходимости сортируем в обратном направлении
    if (this.sortDir === 'desc') {
        sorted = sorted.reverse();
    }
 
    return sorted;
    },
    category() {
            return this.$store.getters.prodByCategory
        },
  },
  methods: {
    Rangeshow() {
      return this.$route.path != '/'
    },
    ...mapActions([
      'GET_PRODUCTS_FROM_API',
      'ADD_TO_CART',
    ]),
    addToCart(data){
      this.ADD_TO_CART(data)
    },
    filterByCategory(value) {
      this.filtredProduct = '';
      return this.$store.dispatch('filterByCategory', +value)
    },
    sortByPrice() {
      let vm = this
      this.filtredProduct = [...this.PRODUCTS]
      this.filtredProduct = this.filtredProducts.filter(function (product) {
        return product.price_new >= vm.minPrice && product.price_new <= vm.maxPrice
      })
    },
    sortProductsBySearchValue(value) {
        this.filtredProduct = [...this.PRODUCTS]
          if (value) {
            this.filtredProduct = this.filtredProduct.filter(function (item) {
              return item.name.toLowerCase().includes(value.toLowerCase())
          })
          } else {
            this.filtredProduct = this.$store.state.filteredByCategory;
          }
    },
  },
watch: {
      SEARCH_VALUE() {
        this.sortProductsBySearchValue(this.SEARCH_VALUE);
      }
},
  mounted() {
    this.GET_PRODUCTS_FROM_API()
    .then((response) => {
          if (response.data) {
            this.filtredProducts()
            this.filterByCategory()
            this.sortProductsBySearchValue()
          }
        })
  }
}
</script>






<style lang="scss" scoped>
    .wrapper1 {
      display: flex;
      // flex-direction: column;
      margin-top: 30px;
    }
    .v-category {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      width: 150px;
      align-items: center;
      margin: 0 50px;
    }
    .v-catalog {
        &__list {
          max-width: 1440px;
          margin: 0 auto;
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          justify-content: space-evenly;
          @media screen and (max-width: 1024px) {
              justify-content: space-evenly;
          }
        }
        &-item {
          @media screen and (max-width: 806px) {
            flex-basis: 35%;
          }
          @media screen and (max-width: 700px) {
            flex-basis: 51%;
          }
        }
        &__sort {
          margin: 0 auto;
          max-width: 700px;
          font-size: 36px;
          display: flex;
          justify-content: space-around;
        }
        &__show-more {
          margin: 0 auto;
          max-width: 1440px;
          & button {
            width: 150px;
            height: 50px;
            background: rgb(13, 148, 13);
            color: #FFF;
          }
        }
    }

</style>