<template>
  <div class="v-register">
    <div class="col-md-6 login-form-1 form">
    <form @submit.prevent="register">
        <h4>Register</h4>
      <label for="name">Name</label>
      <div class="form-group">
          <input id="name" class="form-control" type="text" v-model="name" required autofocus>
      </div>
      <label for="email" >E-Mail Address</label>
      <div class="form-group">
          <input id="email" class="form-control" type="email" v-model="email" required>
      </div>
      <label for="password">Password</label>
      <div class="form-group">
          <input id="password" class="form-control" type="password" v-model="password" required>
      </div>
      <label for="password-confirm">Confirm Password</label>
      <div class="form-group">
          <input id="password-confirm" class="form-control" type="password" v-model="password_confirmation" required>
      </div>
      <div class="form-group">
          <button type="submit" class="btnSubmit">Register</button>
      </div>
      <div class="form-group">
            <a href="#/login" class="ForgetPwd">Sign In!</a>
        </div>
    </form>
    </div>
  </div>
</template>

<script>
    export default {
        props : ["nextUrl"],
        data(){
            return {
                name : "",
                email : "",
                password : "",
                password_confirmation : "",
                is_admin : null
            }
        },
        methods : {
            register: function () {
        let data = {
          name: this.name,
          email: this.email,
          password: this.password,
          password_confirmation: this.password_confirmation,
          is_admin: this.is_admin
        }
        this.$store.dispatch('register', data)
       .then(() => this.$router.push('/'))
       .catch(err => console.log(err))
      }
    }
    }
</script>

<style lang="scss">
    .v-register {
        max-width: 1440px;
        margin: 25px auto;
        display: flex;
        justify-content: center;
        
        & form {
            align-items: center;
        }
    }
</style>